import React,{Component} from 'react';
import {Tabs,Tab, Grid, Cell, Card, CardTitle, CardText, CardActions, Button} from 'react-mdl';

class Projects extends Component{
    constructor(props){
        super(props);
        this.state={activeTab :0};
    }

    togglecategories(){
        if(this.state.activeTab===0){
            return(
                <div className="projects-grid">
                {/*#Project 1*/}
                <Card shadow={0} style={{width: '320px', height: '320px', margin: 'auto'}}>
                <CardTitle expand style={{color: '#fff', 
                background: 'url(https://blog.algolia.com/wp-content/uploads/2015/11/React_illo_final_720x400.png) bottom right 15% no-repeat #46B6AC'}}>
                 #Project 1
                </CardTitle>
                <CardText>
                React is a JavaScript library for building user interfaces. It is maintained by Facebook and a community of individual developers and companies. 
                </CardText>
                <CardActions border>
                    <Button colored>GitHub</Button>
                </CardActions>
            </Card>

            {/*#Project 2*/}
            <Card shadow={0} style={{width: '320px', height: '320px', margin: 'auto'}}>
                <CardTitle expand style={{color: '#fff', 
                background: 'url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTVuN7DQ8PPy8PAVC5_odc_FuI7qipcJ42EcjNcHf5ke8GwWnMzvQ) bottom right 15% no-repeat #46B6AC'}}>
                 #Project 2
                </CardTitle>
                <CardText>
                React is a JavaScript library for building user interfaces. It is maintained by Facebook and a community of individual developers and companies. 
                </CardText>
                <CardActions border>
                    <Button colored>GitHub</Button>
                </CardActions>
            </Card>
             {/*#Project 3*/}
             <Card shadow={0} style={{width: '320px', height: '320px', margin: 'auto'}}>
                <CardTitle expand style={{color: '#fff', 
                background: 'url(http://d585tldpucybw.cloudfront.net/sfimages/default-source/default-album/react_blogreact_870x220_3.png?sfvrsn=108c978d_1) bottom right 15% no-repeat #46B6AC'}}>
                 #Project 3
                </CardTitle>
                <CardText>
                React is a JavaScript library for building user interfaces. It is maintained by Facebook and a community of individual developers and companies. 
                </CardText>
                <CardActions border>
                    <Button colored>GitHub</Button>
                </CardActions>
            </Card>
            </div>
            )
        }

        else if(this.state.activeTab===1){
            return(
                <div className="projects-grid">
                {/*#Project 1*/}
                <Card shadow={0} style={{width: '320px', height: '320px', margin: 'auto'}}>
                <CardTitle expand style={{color: '#fff', 
                background: 'url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcThWZcmzA6GsbzXabF663bjTIXth2PP1iNPq3Av5aEepKgbrabFQg) bottom right 15% no-repeat #46B6AC'}}>
                 #Project 1
                </CardTitle>
                <CardText>
               JavaScript, often abbreviated as JS, is a high-level, interpreted programming language. It is a language which is also characterized as dynamic, weakly typed, prototype-based and multi-paradigm.. 
                </CardText>
                <CardActions border>
                    <Button colored>GitHub</Button>
                </CardActions>
            </Card>

            {/*#Project 2*/}
            <Card shadow={0} style={{width: '320px', height: '320px', margin: 'auto'}}>
                <CardTitle expand style={{color: '#fff', 
                background: 'url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSI3HZuidpyBKmGQfNlXEf2vAVS726rcOVm7-s0rzuv3fhd0V8q) bottom right 15% no-repeat #46B6AC'}}>
                 #Project 2
                </CardTitle>
                <CardText>
                JavaScript, often abbreviated as JS, is a high-level, interpreted programming language. It is a language which is also characterized as dynamic, weakly typed, prototype-based and multi-paradigm.
                </CardText>
                <CardActions border>
                    <Button colored>GitHub</Button>
                </CardActions>
            </Card>
             {/*#Project 3*/}
             <Card shadow={0} style={{width: '320px', height: '320px', margin: 'auto'}}>
                <CardTitle expand style={{color: '#fff', 
                background: 'url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTq6Cl7ZWuxxOd146JvPkvmVZIep-_YhnvmioYaB6jkz4gtj7JNNQ) bottom right 15% no-repeat #46B6AC'}}>
                 #Project 3
                </CardTitle>
                <CardText>
                JavaScript, often abbreviated as JS, is a high-level, interpreted programming language. It is a language which is also characterized as dynamic, weakly typed, prototype-based and multi-paradigm. 
                </CardText>
                <CardActions border>
                    <Button colored>GitHub</Button>
                </CardActions>
            </Card>
            </div>
            )
        }
        else if(this.state.activeTab===2){
            return(
                <div className="projects-grid">
                {/*#Project 1*/}
                <Card shadow={0} style={{width: '320px', height: '320px', margin: 'auto'}}>
                <CardTitle expand style={{color: '#fff', 
                background: 'url(https://d1nzpkv5wwh1xf.cloudfront.net/640/k-57e4def8ce4b145a1020dbf9/20180301-thanhtd09-01032018/python.jpg) bottom right 15% no-repeat #46B6AC'}}>
                 #Project 1
                </CardTitle>
                <CardText>
              Python is an interpreted high-level programming language for general-purpose programming.Python has a design philosophy that emphasizes code readability, notably using significant whitespace. 
                </CardText>
                <CardActions border>
                    <Button colored>GitHub</Button>
                </CardActions>
            </Card>

            {/*#Project 2*/}
            <Card shadow={0} style={{width: '320px', height: '320px', margin: 'auto'}}>
                <CardTitle expand style={{color: '#fff', 
                background: 'url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR5jBs6NCxV2rO07RmV_16mIN51_uH9zYaqWVdiGMKOfDSWiw50Dg) bottom right 15% no-repeat #46B6AC'}}>
                 #Project 2
                </CardTitle>
                <CardText>
               Python is an interpreted high-level programming language for general-purpose programming.Python has a design philosophy that emphasizes code readability, notably using significant whitespace.
                </CardText>
                <CardActions border>
                    <Button colored>GitHub</Button>
                </CardActions>
            </Card>
             {/*#Project 3*/}
             <Card shadow={0} style={{width: '320px', height: '320px', margin: 'auto'}}>
                <CardTitle expand style={{color: '#fff', 
                background: 'url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR0GjPz9MYIC8mFf_3wUlGi57xR-3qx67RDFzbBzobRzi7aMUF2) bottom right 15% no-repeat #46B6AC'}}>
                 #Project 3
                </CardTitle>
                <CardText>
                Python is an interpreted high-level programming language for general-purpose programming.Python has a design philosophy that emphasizes code readability, notably using significant whitespace. 
                </CardText>
                <CardActions border>
                    <Button colored>GitHub</Button>
                </CardActions>
            </Card>
            </div>
            )
        }
    }
    render(){
        return (
            <div /*className="tabs-category"*/>
            <Tabs activeTab={this.state.activeTab} onChange={(tabId) =>this.setState({activeTab:tabId})} ripple>
            <Tab>React</Tab>
            <Tab>Java Script</Tab>
            <Tab>Python</Tab>  
            </Tabs>

            <section>
            <Grid>
                <Cell col={12}>
                    <div /*className="content"*/>{this.togglecategories()} 
                    </div>
                </Cell>
            </Grid>

            </section>
                
            </div>
        )
    }
}
export default Projects;


