import React,{Component} from 'react';
import {Grid,Cell} from 'react-mdl';
import Education from './Education';
class Resume extends Component{
    render(){
        return (
            <div>
                <Grid>
                    <Cell col={4}>
                    <div style={{textAlign:'center'}}>
                        <img src="http://mvpowers.net/img/avatar.png" 
                        alt="avatar"
                        style={{height:"250px"}}/>
                    </div>
                        <h3 style={{textAlign:'center'}}>Sumit Kumar Das</h3>
                        <h4 style={{textAlign:'center',color: 'darkslategray'}}>CSE,NIT SILCHAR</h4>
                        <h5 style={{textAlign:'center',color: 'darkslategray'}}>(Batch of 2020)</h5> 
                        <hr style={{border: '1px dotted black'}}/>
                        <p style={{color: 'darkslategray',fontSize:"20px"}}> A lazy learner,Completed his second year of CSE course.</p>
                        <hr style={{border: '1px dotted black'}}/>
                        <h4 style={{color: 'darkslategray'}}> contact : 8638202113 </h4>
                        <h4 style={{color: 'darkslategray'}}>E-mail : Krsumit449@gmail.com</h4>
                    </Cell>

                    <Cell className="resume-right-column" col={8}>
                    <h3 style={{color:'white',fontFamily:'arial'}}>will modify soon....</h3>


                    <Education StartYear={2018} />
                    
                    
                    </Cell>
                    
                </Grid>
               
            
            </div>
        )
        
    }
}
export default Resume;


