import React,{Component} from 'react';
import {Grid, Cell, List, ListItem,ListItemContent} from 'react-mdl';
class Contact extends Component{
    render(){
        return (
            <div className="body-css">
            <Grid className="grid-css">
                <Cell col={7}>
                <h3> Sumit Kumar Das</h3>
                <img src="https://st2.depositphotos.com/1007566/12294/v/950/depositphotos_122942180-stock-illustration-avatar-man-cartoon.jpg" 
                alt="avatar" style={{height: '300px'}} /> 
                <p style={{width:"250px", margin:"auto", paddingTop:"1em"}}> He's a student of computer science and Engineering and is currently studying in National Institute of Technology,Silchar</p>
                </Cell>

                <Cell col={3}>
                <h2>Contact Me</h2>
                <hr/>
                <div className="contact-css">
                <List>
                    <ListItem>
                        <ListItemContent style={{fontFamily:"anton" ,fontSize:"30px"}}>
                        <i className="fa fa-phone" aria-hidden="true"></i>
                         8638202113
                        </ListItemContent>
                    </ListItem>

                    <ListItem>
                        <ListItemContent style={{fontFamily:"anton" ,fontSize:"21px"}}>
                        <i className="fa fa-envelope-o" aria-hidden="true"></i>
                        Krsumit449@gmail.com
                        </ListItemContent>
                    </ListItem>


                        
                    
                    
                </List>
                </div>

                </Cell>
            </Grid>
             </div>
        )
    }
}
export default Contact;


