import React,{Component} from 'react';
import {Grid, Cell} from 'react-mdl';

class LandingPage extends Component{
    render(){
        return (
            <div style={{width:'100%',  margin:'auto'}}>
               <Grid className="landing-grid">
               <Cell col={12}>
               <img src="https://steemitimages.com/DQmYZ8cdJWyjpKxL2N3XFSDFB4y4gYFeYG5DAAyL2vnL9ga/376.jpg"
                alt="Avatar"
                className="avatar-img"/>

                <div className="text">
                    <h1>A Student Of Computer Science and Engineering.
                </h1>
                </div>

                <div className="social-links" >

                    <a className = "icon"  href="https://www.linkedin.com/in/sumit-kumar-das-87a599158/" rel="noopener noreferrer" target="_blank">
                       <i className="fa fa-linkedin-square" aria-hidden="true" />
                    </a>

                     <a className = "icon" href="https://github.com/sumitNITS" rel="noopener noreferrer" target="_blank">
                        <i class="fa fa-github" aria-hidden="true"/>
                    </a>

                    <a className = "icon" href="https://twitter.com/sumitkr449" rel="noopener noreferrer" target="_blank">
                        <i class="fa fa-twitter" aria-hidden="true"/>
                    </a>

                    <a className = "icon" href="https://www.facebook.com/sumitkumar058" rel="noopener noreferrer" target="_blank">
                        <i className="fa fa-facebook-official" aria-hidden="true" />
                    </a>

                    <a className = "icon" href="https://www.instagram.com/s_a_m_058/" rel="noopener noreferrer" target="_blank">
                        <i className="fa fa-instagram" aria-hidden="true" />
                    </a>
                </div>

               </Cell>
               </Grid>
            </div>
        )
    }
}
export default LandingPage;


